<script>
import datetimePicker from '../../components/datatime-picker/datetime-picker.vue';
import UploadImage from '../../components/upload-image/upload-image.vue';
import stepsList from '../../components/steps-list/steps-list.vue'
  export default {
    components: {
      datetimePicker,
      UploadImage,
      stepsList
    },
    data() {
      return {
        title: 'input',
        defaultValue: '', // 默认值
        dataList: [],
        imgItems: [],
				types: 'image',
        params: {},
        list: [
          
        ]
      }
    },
    onLoad(options) {
      console.log(options.id)
      this.$http.rescueInfoDetail({'id':options.id}).then(res=>{
        console.log('res =>',res)
        this.params = res.data
        console.log(this.params)
        let type = this.params.type
        if(type == 1){
          this.params.typeName = '车辆事故'
        }else if(type == 2){
          this.params.typeName = '车辆故障'
        }else if(type == 3){
          this.params.typeName = '应急停车'
        }
        this.imgItems = this.params.fileUrls
        let dto = res.data.rescueInfoDetailDTOS
        console.log('dto =>',dto)
        let array = []
        dto.forEach(e => {
          if(e.status == 1){
            e.title = '用户申请紧急定制路线'
          }else if(e.status == 2){
            e.title = '已受理'
          }else if(e.status == 3){
            e.title = '协调'
          }else if(e.status == 4){
            e.title = '处理完成'
          }else if(e.status == 5){
            e.title = '已取消'
          }else if(e.status == 6){
            e.title = '已驳回'
          }else if(e.status == 9){
            e.title = '处理结束'
          }
          let plan = {
            title: e.title,
            date: e.createTime,
            rejectedRemark: e.rejectedRemark,
            status: e.status
          }
          array.push(plan)
        })
        console.log('array =>',array)
        this.list = array
        console.log('statusDTO =>',this.statusDTO)
      })
			// this.params = JSON.parse(options.params)
      
		},
    methods: {
      saveInfo: function(){
        uni.navigateTo({
          url:'/pages/rescue/rescueInfoSure'
        });
      },
      previewImg() {
        wx.previewImage({
					urls: this.params.fileUrls, //需要预览的图片http链接列表，多张的时候，url直接写在后面就行了
					current: this.params.fileUrls[0], // 当前显示图片的http链接，默认是第一个
					success: function(res) {},
					fail: function(res) {},
					complete: function(res) {},
				})
		  },
      openActive(item,index){
        this.$nextTick(() =>{
          this.listQuery = item
          this.paymentToDate = item.vipCardType
          if(index === 0){
            this.paymentTypeItems[index].isactive = 0
            this.paymentTypeItems[index+1].isactive = 1
            this.paymentTypeItems[index+2].isactive = 1
          }else if(index === 1){
            this.paymentTypeItems[index].isactive = 0
            this.paymentTypeItems[index-1].isactive = 1
            this.paymentTypeItems[index+1].isactive = 1
          }else if(index === 2){
            this.paymentTypeItems[index].isactive = 0
            this.paymentTypeItems[index-1].isactive = 1
            this.paymentTypeItems[index-2].isactive = 1
          }
        })
      },
    },
  }
</script>

